footer {
  height: auto;
  width: 100%;
  padding-top: 2.5em;
  background: #4b3f72;
  color: white;

  .content_footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;

    h2 {
      font-size: 1.438em;
      font-weight: 400;
      text-transform: capitalize;
      line-height: 3rem;
    }

    ul {
      #list_menu {
        display: flex;
        justify-content: space-around;
        align-items: center;
      }
      li {
        a {
          font-size: 1.625em;
          color: white;
          padding: 0 1em;
          display: block;
          font-weight: 500;
        }
      }
    }
  }

  .footer_bottom {
    background: #1f2041;
    width: 100%;
    padding: 1.25em 0;
    text-align: center;
    p {
      font-size: 1.063em;
      text-transform: capitalize;
      word-spacing: 0.125em;
    }
  }
}

.social_icons_footer {
  .social_icons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem 0 3rem 0;

    li {
      margin: 0 10px;
      .icon_a {
        padding: 0 0.7em;
        &:hover {
          cursor: pointer;
        }
      }
    }
  }
  img {
    width: 30px;
    height: 30px;
  }
}
