@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700&display=swap");
@import "./abstracts/variables";
@import "./abstracts/mixins.scss";
@import "./pages/countdown.scss";

body {
  height: 100vh;
  margin: 0;
  font-family: "Poppins", arial;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  color: $white_color;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
}

.nav-container {
  background: $dark_violet_color_;
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 5;

  a {
    color: $white_color;
  }
}

.html5_img,
.css3_img,
.js_img {
  cursor: pointer;
}

@import "./layouts/header";

.hamburger {
  @include displayNone();
  .bar {
    @include displayBlock();
    width: 25px;
    height: 3px;
    margin: 5px auto;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    background-color: $white_color;
  }
}

.social-header,
.hero-design {
  @include displayNone();
}

.hero {
  background: $violet_color_;
  color: $white_color;
  @include grid();
  text-align: center;
  padding: 4em;
}

.meet {
  @include displayFlex(flex);
  margin: 0 auto;
  width: 205px;
  font-weight: bold;

  span {
    margin-top: 0em;
    margin-right: 0.5em;
    color: $cyan;
  }
}

.meet > p {
  color: $yellow_color_;
}

.scroll {
  width: 30px;
  margin-top: 2em;
}

.about {
  background: $dark_white;
  p {
    color: $gray_color_;
  }
}

section {
  padding: 4em 2em;
  text-align: center;
  position: relative;
}

.main {
  padding: 3em;
  text-align: center;
  position: relative;
}

.my_button {
  width: 90px;
  color: $violet_color_;
  font-weight: bold;
  cursor: pointer;
  border: none;
  border-radius: 16px;
  padding: 0.313em;
  font-size: 1em;
  background: none;

  &:hover {
    background-color: $yellow_color_;
  }

  &:focus {
    outline: none;
  }
}

.hide {
  display: none;
}

.svg {
  width: 200px;
  height: 200px;
  position: absolute;
  top: 20%;
  left: 50%;
  // left: 690px;
  pointer-events: none;
}
.featured,
.featured2 {
  position: relative;
}

.featured::before,
.featured2::before {
  content: "";
  position: absolute;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: $violet_color_;
  top: 0;
  left: 0;
  z-index: -1;
  opacity: 0.4;
}

.right {
  width: 200px;
  height: 200px;
}

.subtitle {
  text-transform: uppercase;
  font-weight: bold;
  color: $dark_blue;
  letter-spacing: 0.2em;
  font-size: 0.85em;
}

.featured-title {
  color: $black_color;
  font-weight: bold;
  font-size: 1.3em;
  margin-top: -0.4em;
  @include displayBlock();
}

.featured-desc {
  color: $gray_color_;
  margin-bottom: 3em;
  font-size: 0.9em;
  line-height: 1.8em;
  font-weight: 500;
}

@import "./layouts/skill_container";
section > .little-text {
  background: $dark_white;
}

@media screen and (max-width: 768px) {
  /*What this media query does is, hides our nav-menu by setting position: fixed; left: -100%; on it.
Also, we set our hamburger to display: block; so it's visible now.

We have also added an extra class that is .nav-menu.active which sets left: 0; on the nav-menu. 
Now we will have to add the javascript which will add this active class on the nav-menu when we click our hamburger.
*/

  .nav-menu {
    position: fixed;
    right: 0;
    top: 5rem;
    flex-direction: column;
    width: 100%;
    height: 425px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 10px 27px $box_shadow_color;
    clip-path: circle(100px at 90% -25%);
    -webkit-clip-path: circle(100px at 90% -25%);
    transition: all 1.3s ease-out;
    pointer-events: none;
    z-index: 1;
    background: $dark_violet_color_;
  }

  .nav-menu.active {
    left: 0;
    clip-path: circle(1000px at 90% -10%);
    -webkit-clip-path: circle(1000px at 90% -10%);
    pointer-events: all;
    cursor: pointer;
  }

  .nav-item {
    margin: 2rem 0;
  }

  ul.nav-menu.active {
    background: $dark_violet_color_;
    z-index: 1;
    height: 425px;

    position: absolute;
  }

  .hamburger {
    display: block;
    cursor: pointer;
  }

  .hamburger.active .bar:nth-child(2) {
    opacity: 0;
  }

  .hamburger.active {
    span {
      &:nth-child(1) {
        transform: translateY(8px) rotate(45deg);
      }
    }
  }

  .hamburger.active {
    span {
      &:nth-child(2) {
        transform: translateY(8px) rotate(45deg);
      }
    }
  }

  .hamburger.active {
    span {
      &:nth-child(3) {
        transform: translateY(-8px) rotate(-45deg);
      }
    }
  }

  .nav-menu {
    li {
      opacity: 0;
    }
  }

  ul {
    li {
      a {
        font-size: 1.3em;
      }
    }
  }

  ul {
    li {
      &:nth-child(1) {
        transition: all 0.5s ease 0.2s;
      }
    }
  }

  ul {
    li {
      &:nth-child(2) {
        transition: all 0.5s ease 0.4s;
      }
    }
  }

  ul {
    li {
      &:nth-child(3) {
        transition: all 0.5s ease 0.6s;
      }
    }
  }

  ul {
    li {
      &:nth-child(4) {
        transition: all 0.5s ease 0.8s;
      }
    }
  }

  li.fade {
    opacity: 1;
  }

  .above-footer {
    height: 150vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
  }

  div.container1 {
    position: relative;
    background: $dark_cyan;
    width: 360px;
    height: 360px;
    z-index: 1;
    border: 1px solid $dark_cyan;
    .cont {
      width: 360px;
      height: 360px;
      transform: rotate(-4deg);
      text-transform: uppercase;
    }
    img {
      width: 250px;
      height: 250px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      opacity: 0.6;
      z-index: -1;
    }

    p {
      color: $white_color;
      font-weight: bold;
      font-size: 4em;
      word-break: normal;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-35%, -77%);
    }
  }

  div.container2 {
    width: 360px;
    height: 360px;
    position: relative;
    z-index: 1;
    border: 1px solid $white_color;
    .cont {
      width: 360px;
      height: 360px;
      transform: rotate(-4deg);
      text-transform: uppercase;
    }

    img {
      width: 250px;
      height: 250px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      opacity: 0.6;
      z-index: -1;
    }

    p {
      color: $black_color;
      font-weight: bold;
      font-size: 4em;
      word-break: normal;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-35%, -77%);
    }
  }

  div.container3 {
    width: 360px;
    height: 360px;
    position: relative;
    background: $dark_cyan;
    z-index: 1;
    border: 1px solid $dark_cyan;

    .cont {
      width: 360px;
      height: 360px;
      transform: rotate(-4deg);
      text-transform: uppercase;
    }

    img {
      width: 250px;
      height: 250px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      opacity: 0.6;
      z-index: -1;
    }

    p {
      width: 40%;
      color: $white_color;
      font-weight: bold;
      font-size: 4em;
      word-break: normal;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-35%, -77%);
    }
  }

  @import "./layouts/social_footer";
}

.list_menu {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

@media only screen and (max-width: 629px) {
  ul.list_menu {
    flex-direction: column;
  }
}
