@media only screen and (min-width: 1050px) {
  .above-footer {
    width: 100%;
    height: 75vh;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  .hero {
    height: 110vh;
  }

  .nav-container {
    display: grid;
    grid-template-columns: 66% auto;
    background: unset;
  }

  header {
    background: $dark_violet_color_;
  }

  header {
    padding: 2em 2em 2em 4em;
  }

  .social-header {
    padding: 2em 2em 4em 2em 0;
  }

  section {
    padding: 4em;
  }

  @import "./layouts/social_header";

  .hero {
    @include displayGrid(grid, 66% auto);
    background: unset;
    padding: 0;

    .content {
      background: $violet_color_;
      padding: 6em 8em 6em 4em;
      text-align: left;

      h1 {
        font-size: 3em;
        line-height: 1.2em;
      }
    }
  }

  .meet {
    margin: unset;
  }

  //this is hero design
  .hero-design {
    @include displayUnset();
    margin-left: -55%;
    margin-top: 15%;
    width: 100%;
  }

  @import "./layouts/social_footer";
}

