.my-skills {
  background: $yellow_color_;
}

.skills-container ul li {
  background: $white_color;
  padding: 2em;
  border-radius: 1em;
  margin-bottom: 1em;

  .icon-container {
    height: 100px;
    display: grid;
    place-content: center;
    margin: 0 auto;

    &.one {
      width: 50px;
    }
    &.two {
      width: 50px;
    }
    &.three {
      width: 50px;
    }
  }

  .skills-title {
    font-weight: bold;
    color: $black_color;
  }

  .skills-desc {
    margin-bottom: 2em;
  }
}
